<template>
  <div>
    <vs-card class="p-5">
      <div class="flex flex-wrap">
        <div class="w-full sm:w-1/2 md:w-1/2 lg:w-1/2 xl:w-1/2">
          <a href="javascript:void()" @click="$router.go(-1)">← Back</a>
          <h4 class="inline-block ml-2">{{$route.query.churchName }}'s Branch</h4>
        </div>
        <div class="w-full sm:w-1/2 md:w-1/2 lg:w-1/2 xl:w-1/2 text-right">
          <vs-button
            color="primary"
            :to="'/create-branch/'+$route.query.publicToken"
            type="filled"
          >Create Church Branch</vs-button>
        </div>
      </div>

      <vs-table
        class="vs-con-loading__container"
        id="div-with-loading"
        search
        max-items="5"
        pagination
        :data="churchBranch"
      >
        <template slot="thead">
          <!-- <vs-th sort-key="email">Id</vs-th> -->
          <vs-th>Id</vs-th>
          <vs-th>Date created</vs-th>
          <vs-th>Branch Name</vs-th>
          <vs-th>Pastor</vs-th>
          <vs-th>Email</vs-th>
          <vs-th>Location</vs-th>
          <vs-th>Action</vs-th>
        </template>

        <template slot-scope="{data}">
          <vs-tr :key="index" v-for="(tr, index) in data">
            <vs-td :data="tr.id">{{ index + 1 }}</vs-td>
            <vs-td :data="tr.createdAt">{{ tr.createdAt | formatDate }}</vs-td>
            <vs-td :data="tr.name">{{ tr.name }}</vs-td>
            <vs-td>{{tr.pastor}}</vs-td>
            <vs-td>
              <a :href="'mailto:'+tr.email" target="_blank">{{tr.email}}</a>
            </vs-td>
            <vs-td>{{tr.state}}, {{tr.country}}</vs-td>
            <vs-td>
              <vs-button
                @click="$router.push({
                  path: '/view-branch',
                  query: { branchId: tr._id, branchName: tr.name },
                })"
                color="primary"
                type="filled"
                size="small"
                class="mr-2 mb-2"
              >View Branch</vs-button>
            </vs-td>
          </vs-tr>
        </template>
      </vs-table>
    </vs-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      churchBranch: []
    };
  },
  methods: {
    openLoadingInDiv() {
      this.$vs.loading({
        container: "#div-with-loading",
        scale: 0.6
      });
    },
    getChurchBranch() {
      const churchId = this.$route.query.churchId;
      this.$store
        .dispatch("getChurchBranch", churchId)
        .then(resp => {
          this.$vs.loading.close("#div-with-loading > .con-vs-loading");
          console.log(resp.data);
          this.churchBranch = resp.data;
        })
        .catch(err => {
          this.$vs.loading.close("#div-with-loading > .con-vs-loading");
          console.log(err.response);

          if (err.response) {
            if (err.response.status == 400) {
              console.log(err.response.data);
              this.$vs.notify({
                title: "Church Branch",
                text: err.response.data,
                color: "warning"
              });
            } else {
              this.$vs.notify({
                title: "Church Branch",
                text: "Unable to load church branch, try again!",
                color: "warning"
              });
            }
          } else {
            this.$vs.notify({
              title: "Church Account",
              text: "Unable to load church branch, try again!",
              color: "warning"
            });
          }
        });
    }
  },
  mounted() {
    this.openLoadingInDiv();
    this.getChurchBranch();
  }
};
</script>